interface OffersCardProps {
    image: string;
    title: string;
    description: string;
    delay:any;
  }
  
  const OffersCard: React.FC<OffersCardProps> = ({ image, title, description , delay }) => {
    return (
      <li data-aos="fade-up" 
      data-aos-delay={delay}
      className="offers-card">
        <div className="photo">
          <img src={image} alt="Offer" />
        </div>
        <h2 className="offers-card-title">{title}</h2>
        <p className="offers-card-descrption">{description}</p>
      </li>
    );
  };
  
  export default OffersCard;
  