import MainOfferSection from "../Components/MainOffersSection";
import QuestionPhoto from "../../assets/Images/Screenshot_20250124_165516_com.qualityconnect 2.png";

import "../Style/Offers.css";
import DownloadLinks from "../../Shared/Components/Elements/DownloadLinks.";
import TestPhoto from "../../assets/Images/unnamed 2.png";

import Aos from "aos";
import "aos/dist/aos.css"; // استيراد CSS الخاصة بـ AOS
import { useEffect } from "react";
import OffersCard from "../Components/OffersCard";

const Offers = () => {
    const offers = [
        {
          image: "https://example.com/image1.jpg",
          title: "إشتري عبر شلن وأحصل على كاش باك 10%",
          description: "مع شلن، ادفع فواتيرك والخدمات الحكومية بسهولة وبدون أي رسوم إضافية. وفر الوقت والمال واجعل حياتك أبسط.",
        },
        {
          image: "https://example.com/image2.jpg",
          title: "عرض خاص على المدفوعات",
          description: "وفر 20% عند الدفع باستخدام شلن للمطاعم والمقاهي المختارة.",
        },
        {
          image: "https://example.com/image3.jpg",
          title: "احصل على نقاط مكافآت عند الدفع عبر شلن",
          description: "كل عملية دفع عبر شلن تمنحك نقاط مكافآت يمكن استبدالها بعروض وهدايا حصرية.",
        },
        {
          image: "https://example.com/image4.jpg",
          title: "خصومات تصل إلى 30% عند التسوق عبر شلن",
          description: "استخدم شلن في عمليات الشراء أونلاين واحصل على خصومات حصرية تصل إلى 30%.",
        },
        {
          image: "https://example.com/image5.jpg",
          title: "سحب مجاني من أجهزة الصراف عبر شلن",
          description: "قم بالسحب النقدي مجانًا من أي جهاز صراف يدعم شلن دون أي رسوم إضافية.",
        },
        {
          image: "https://example.com/image6.jpg",
          title: "استرداد نقدي 5% على حجوزات الفنادق",
          description: "ادفع عبر شلن عند حجز الفنادق واحصل على استرداد نقدي 5% تلقائيًا.",
        },
        {
          image: "https://example.com/image7.jpg",
          title: "دفع الفواتير بسهولة بدون رسوم",
          description: "ادفع فواتير الكهرباء والماء والهاتف عبر شلن دون أي رسوم إضافية واستمتع براحة البال.",
        },
      ];
      


    useEffect(() => {
        Aos.init({
            duration: 800,
            easing: "ease-in-out",
            once: true,
        });
    }, []);
    return (
        <div className="offers">
            <MainOfferSection
                imageSrc={QuestionPhoto}
                className="main-offers"
                dir={false}
            >
                <>
                    <h1 className="section-title">
                        مع عروض شلن <span>أمورك طيبة</span> وبدون مشاكل
                    </h1>
                    <p className="section-description">
                        نحن هنا لجعل إدارة نفقاتك اليومية أكثر سهولة وراحة ، لدينا الحلول
                        المثلى التي تلبي احتياجاتك.
                    </p>
                    <DownloadLinks />
                </>
            </MainOfferSection>
            <div className="offers-container">
                <div className="section-header">
                    <h1 className="title">جميع العروض</h1>
                </div>
                <ul className="offers-content">
                    {offers.map((offer, index) => (
                        <OffersCard
                            key={index}
                            image={TestPhoto}
                            delay={index*100}
                            title={offer.title}
                            description={offer.description}
                        />
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default Offers;
